.game {
  border: solid 4px #bababa;
  height: 620px;
  width: 360px;
  margin: 40px auto;
  border-bottom-right-radius: 15% 10%;
  border-bottom-left-radius: 7.5% 5%;
}

@media screen and (max-width: 411px) {
  .game {
    border: none;
    width: 100%;
    margin-top: 0;
  }

  body {
    overflow: hidden;
  }
}

.canvas-container {
  position: relative;
  border: solid 4px #bababa;
  margin: 20px auto;
  justify-content: center;
}

.canvas-overlay {
  display: flex;
  position: absolute;
  background: #ccc;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.overlay-message {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  opacity: 1;
  color: tomato;
  text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
  text-align: center;
  font-size: 30px;
}

.snake-img {
  width: 150px;
  height: 150px;
  margin-bottom: -50px;
}

.overlay-button {
  border: solid 2px #2ec4b6;
  color: #2ec4b6;
  background: #fdfffc;
  padding: 5px 10px;
  margin-top: 20px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
  outline: none;
}

.overlay-button:active {
  transform: translateY(2px);
}

#canvas {
  background: #2ec4b6;
}

.under-canvas {
  text-align: center;
  color: white;
  background: #bababa;
  padding: 2px;
}

.controls {
  display: flex;
  margin: 10px auto;
  padding: 2px;
  justify-content: space-between;
}

.directional-container {
  background: #ccc;
  border: solid 4px #ccc;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  padding: 8px;
  transform: rotate(45deg);
  outline: none;
}

.control {
  font-size: 18px;
  border: solid 3px #ccc;
  width: 70px;
  height: 70px;
  background: #dedede;
  outline: none;
  user-select: none;
}

.control:hover {
  background: #bbb;
}

.control-text {
  color: transparent;
  transform: rotate(-45deg);
}

.up {
  border-top: solid 4px #ccc;
  border-left: solid 4px #ccc;

  border-top-left-radius: 50%;
}

.up:active {
  transform: translate(-2px, -2px);
}

.right {
  border-top: solid 4px #ccc;
  border-right: solid 4px #ccc;

  border-top-right-radius: 50%;
}

.right:active {
  transform: translate(2px, -2px);
}

.down {
  border-bottom: solid 4px #ccc;
  border-right: solid 4px #ccc;

  border-bottom-right-radius: 50%;
}

.down:active {
  transform: translate(2px, 2px);
}

.left {
  border-bottom: solid 4px #ccc;
  border-left: solid 4px #ccc;

  border-bottom-left-radius: 50%;
}

.left:active {
  transform: translate(-2px, 2px);
}

.score {
  border: solid 3px #ccc;
  position: relative;
  left: -7px;
  background: #accac7;
  color: teal;
  text-align: right;
  font-size: 30px;
  padding: 15px 10px;
  width: 120px;
  height: 30px;
  margin: auto;
  text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
}

.score--best {
  color: tomato;
}

.best-score {
  font-size: 16px;
}

.best-score--beaten {
  color: teal;
}

.best-score-label {
  color: #666;
}
